.football-scroll-btn{position:fixed;bottom:30px;right:120px;z-index:999;border:none;outline:none;background:transparent;cursor:pointer;padding:0;width:50px;height:50px;visibility:hidden;opacity:0;transform:scale(0.8);transition:all .3s ease;overflow:visible}.football-scroll-btn.visible{visibility:visible;opacity:1;transform:scale(1)}.football-scroll-btn:hover{transform:scale(1.1)}.football-scroll-btn:active{transform:scale(0.95)}.football-scroll-btn.animating{animation:footballKick .6s ease-out}@keyframes footballKick{0%{transform:scale(1) rotate(0deg)}50%{transform:scale(1.2) rotate(-15deg)}100%{transform:scale(1) rotate(0deg)}}.football-icon{width:100%;height:100%;background:linear-gradient(135deg, #fff 0%, #f0f0f0 100%);border-radius:50%;display:flex;align-items:center;justify-content:center;position:relative;box-shadow:0 4px 15px rgba(0,0,0,.2),inset 0 2px 5px rgba(255,255,255,.8);border:2px solid #333;transition:all .3s ease}.football-icon::before{content:"";font-size:24px;animation:footballSpin 3s linear infinite}@keyframes footballSpin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}.football-icon::after{content:"";position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);width:80%;height:80%;border:2px solid #333;border-radius:50%;opacity:.3;animation:footballPulse 2s ease-in-out infinite}@keyframes footballPulse{0%,100%{transform:translate(-50%, -50%) scale(1);opacity:.3}50%{transform:translate(-50%, -50%) scale(1.1);opacity:.6}}.football-scroll-btn:hover .football-icon{box-shadow:0 6px 20px rgba(0,0,0,.3),inset 0 2px 5px rgba(255,255,255,.9);transform:translateY(-2px)}.football-scroll-btn:hover .football-icon::before{animation-duration:1s}.dark-mode .football-icon{background:linear-gradient(135deg, #2c3e50 0%, #34495e 100%);border-color:#ecf0f1;box-shadow:0 4px 15px rgba(0,0,0,.4),inset 0 2px 5px rgba(255,255,255,.1)}.dark-mode .football-icon::after{border-color:#ecf0f1}.dark-mode .football-scroll-btn:hover .football-icon{box-shadow:0 6px 20px rgba(0,0,0,.5),inset 0 2px 5px rgba(255,255,255,.2)}@media(max-width: 768px){.football-scroll-btn{bottom:20px;right:90px;width:45px;height:45px}.football-icon::before{font-size:20px}}@media(max-width: 480px){.football-scroll-btn{bottom:15px;right:75px;width:40px;height:40px}.football-icon::before{font-size:18px}}.football-scroll-btn{opacity:0;animation:buttonFadeIn 1s ease-out .5s forwards}@keyframes buttonFadeIn{from{opacity:0;transform:scale(0.5) translateY(20px)}to{opacity:1;transform:scale(1) translateY(0)}}